.RContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;

  background-image: url("./images/store_bg.png");
  background-size: 100vw;
}

.HomeContainer {
  margin-top: 5vh;
  font-size: 40px;
  cursor: pointer;
}

.HomeContainer h1 {
  transition: 0.3s ease-in-out;
}

.HomeContainer h1:hover {
  color: rgb(255, 119, 0);
  transform: scale(1.05);
}

.MainFilter {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
  width: 60%;
  margin-top: 5vh;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);

  /* border: solid rgb(255, 0, 225) 2px; */
}

.TypeBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 10px;
  height: 40px;
  width: 140px;
  /* border: solid yellow 1px; */
  color: rgb(42, 42, 42);
  /* border-radius: 20px; */
  margin: 5px;
  font-size: 32px;
  cursor: pointer;
  text-align: center;

  background-image: url("./images/filterbtn.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.TypeBtn p {
  margin: 0;
  padding: 0;
}

.TypeBtn1 {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 10px;
  height: 40px;
  width: 140px;
  /* border: solid rgb(255, 119, 0) 2px; */
  color: rgb(255, 119, 0);
  /* border-radius: 20px; */
  margin: 5px;
  font-size: 32px;
  cursor: pointer;
  text-align: center;

  background-image: url("./images/filterbtn1.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.HL {
  color: rgb(255, 119, 0);
}

.SecFilter {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  margin: 0;
  width: 60%;
  height: 180px;
  margin-top: 40px;

  /* border: solid yellow 2px; */
}

.SecFilter p {
  font-size: 24px;
  margin: 0;
  padding: 0;
  transition: 0.3s ease-in-out;
}

.SecFilter p:hover {
  color: rgb(255, 119, 0);
}

.SFAlign {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: left;
  justify-content: space-around;
  width: 180px;
  margin-top: 40px;
  padding: 20px;
  border-radius: 10px;

  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);

  /* border: solid yellow 2px; */
  margin: 20px;
}

.Display {
  cursor: pointer;
  /* border: solid yellow 2px; */
}

.CardContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  margin: 0;
  width: 60%;
  margin-top: 15vh;
}

.Card {
  display: flex;
  flex-direction: column;
  height: 500px;
  width: 300px;
  align-items: center;
  padding-top: 10px;
  /* border: solid yellow 2px; */
  /* border-radius: 25px; */
  color: #14070a;
  margin: 10px;
  transition: 0.3s ease-in-out;

  background-image: url("./images/scroll.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.Card:hover {
  transform: scale(1.05);
}

.Card h1 {
  text-align: center;
  margin: 0;
  margin-bottom: 30px;
}

.Card h2 {
  /* margin: 0; */
}

.Card p {
  font-size: 18px;
  font-weight: bolder;
  margin: 0;
}

.Card img {
  height: 230px;
}

.CardDesc {
  max-width: 230px;
}
