@import url("https://fonts.googleapis.com/css2?family=VT323&display=swap");

/* font-family: 'VT323', monospace; */

body {
  margin: 0;
  font-family: "VT323", monospace;

  /*colors*/
  --color1: #14070a;
  --color2: #dfcb82;
  --color3: orange;
  --color4: rgb(154, 38, 38);

  --text-focus: rgb(255, 170, 12);
  --text-secondary: white;

  background: var(--color1);
  color: var(--color2);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.CaveImage {
  z-index: 20;
}

/*=========HOME==========*/

.Title {
  text-align: center;
  font-size: 20px;
  margin: 0;
}

.Title h1 {
  margin: 0;
  /* animation: floating 3s infinite ease-in-out; */
}

.Title span {
  color: rgb(255, 119, 0);
  font-size: 72px;
  margin: 0;
}

@keyframes floating {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10);
  }
  100% {
    transform: translateY(0);
  }
}

.LinksContainer {
  /* padding-left: 90px; */
  padding-bottom: 50px;
  display: flex;
  flex-direction: row;
  transform: translateX(-18px);

  /* border: solid red 2px; */
}

.LinksCenter {
  padding-right: 0px;
  padding-left: 0px;
  display: flex;
  flex-direction: column;
}

.LinksLeft {
  padding-top: 150px;
  display: flex;
  flex-direction: column;
}

.LinksRight {
  padding-top: 150px;
  display: flex;
  flex-direction: column;
}

.Link1 {
  height: 100px;
  width: 140px;

  /* border: solid red 2px; */
}

.Link2 {
  height: 240px;
  width: 140px;
  cursor: pointer;

  /* border: solid rgb(0, 255, 4) 2px; */
}

.Link3 {
  height: 70px;
  width: 100px;

  /* border: solid rgb(0, 8, 255) 2px; */
}

.Link4 {
  height: 70px;
  width: 100px;

  /* border: solid rgb(255, 0, 212) 2px; */
}

/*=============RARITY=============*/

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  /* border-radius: 6px; */
  background-color: rgb(136, 89, 156);
  border: solid 2px black;
}

::-webkit-scrollbar-thumb {
  /* border-radius: 6px; */
  border: solid 2px black;
  background-color: rgb(63, 23, 96);
}

::-webkit-scrollbar-thumb:hover {
  background-color: rgb(85, 31, 129);
}
